<script lang="ts" setup>
import { onMounted, ref } from 'vue'
import NewRoute from './NewRoute.vue'
import { deleteRoute, getAllRoutes } from '../../core/requests/Routes'
import { useToast } from 'vuestic-ui'

const { notify } = useToast()

const showNewRoute = ref(false)
const showNewToll = ref(false)
const showConfirm = ref(false)
const isDeleting = ref(false)
const isLoading = ref(false)
const selected = ref(null)
const routes: any = ref([])

const closeModal = () => {
  showNewRoute.value = false
  showNewToll.value = false
  getRoutes()
}

onMounted(() => {
  getRoutes()
})

const getRoutes = async () => {
  isLoading.value = true
  const _routes: any = await getAllRoutes()
  routes.value = _routes
  isLoading.value = false
}

const doDelete = async () => {
  showConfirm.value = false
  await deleteRoute(selected.value)

  notify({
    message: 'Ruta eliminada correctamente',
    color: 'success',
  })

  getRoutes()
}

const needConfirm = (id: any) => {
  selected.value = id
  showConfirm.value = !!id
}

const selectedRoute: any = ref(null)
const showUpdate = (data: any) => {
  if (data) {
    selectedRoute.value = data
    showNewRoute.value = true
  } else {
    selectedRoute.value = data
    showNewRoute.value = false
  }
}
</script>

<template>
  <div class="flex flex-col space-y-6 md:space-y-4">
    <VaInnerLoading :loading="isLoading">
      <VaCard>
        <VaCardTitle class="d-flex justify-between">
          Maestro de Rutas
          <VaButton icon="add" color="primary" @click="showNewRoute = true">
            <span class="px-1">Nueva Ruta</span>
          </VaButton>
        </VaCardTitle>
        <VaCardContent>
          <div class="flex flex-col p-4 bg-backgroundSecondary rounded-lg">
            <div class="va-table-responsive">
              <table class="va-table w-full text-xs">
                <thead>
                  <tr>
                    <th>#</th>
                    <th>Origen</th>
                    <th>Destino</th>
                    <th>Kilometros</th>
                    <th>P. Base</th>
                    <th>Horas</th>
                    <th>Peajes</th>
                    <th></th>
                  </tr>
                </thead>
                <tbody>
                  <tr v-for="t in routes" :key="t.id">
                    <td>{{ t.id }}</td>
                    <td>{{ t.from }}</td>
                    <td>{{ t.to }}</td>
                    <td>{{ t.kilometers || '-' }}</td>
                    <td>S/ {{ t.baseprice }}</td>
                    <td>{{ t.travelhours || '-' }}</td>
                    <td>
                      <p v-if="!t.tolls.length" class="m-0 font-bold text-xs">Sin Información</p>

                      <div v-if="t.tolls.length" class="bg-blue-100 px-2 pb-2 pt-1 rounded-md">
                        <div v-if="t.tolls.filter((f: any) => f.type === 0).length">
                          <p class="text-xs font-bold mb-1 px-1">Ida:</p>
                          <VaBadge
                            v-for="i in t.tolls.filter((f: any) => f.type === 0)"
                            :key="i"
                            :text="'(' + i.vehicle_type + ')' + ' S/' + i.amount"
                            color="primary"
                            class="mr-2"
                          />
                        </div>
                        <div v-if="t.tolls.filter((f: any) => f.type === 1).length">
                          <p class="text-xs font-bold mt-2 mb-1 px-1">Vuelta:</p>
                          <VaBadge
                            v-for="i in t.tolls.filter((f: any) => f.type === 1)"
                            :key="i"
                            :text="'(' + i.vehicle_type + ')' + ' S/' + i.amount"
                            color="primary"
                            class="mr-2"
                          />
                        </div>
                      </div>
                    </td>
                    <td>
                      <div class="flex gap-1">
                        <VaButton icon="edit" color="primary" round @click="showUpdate(t)"></VaButton>
                        <VaButton icon="delete" color="danger" round @click="needConfirm(t.id)"></VaButton>
                      </div>
                    </td>
                  </tr>
                </tbody>
              </table>
            </div>
          </div>
        </VaCardContent>
      </VaCard>
    </VaInnerLoading>

    <VaModal
      v-model="showNewRoute"
      size="700px"
      close-button
      hide-default-actions
      no-dismiss
      blur
      @close="showUpdate(null)"
    >
      <div>
        <NewRoute :data="selectedRoute" @close="closeModal" />
      </div>
    </VaModal>

    <VaModal v-model="showConfirm" size="250px" hide-default-actions no-dismiss blur>
      <p class="text-sm">¿Deseas continuar?</p>

      <template #footer>
        <VaButton size="small" preset="secondary" class="mr-1" @click="needConfirm(null)"> Cancelar</VaButton>
        <VaButton size="small" :loading="isDeleting" @click="doDelete"> Continuar</VaButton>
      </template>
    </VaModal>
  </div>
</template>
